import React,{Component} from 'react';
import store from '../store.js';
import datatext from '../dataTest.js';
import Find from './find.js';
import Dingdan from './dingdan.js';
import Wode from './wode.js';
import Search from './search.js';

class FirstPageTop extends Component{
	constructor(){
		super();
		this.state={
		listArr:datatext.listPage.meishi,}
		this.changePage = this.changePage.bind(this);
	}
	
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
			
	
	changePage(e){
		console.log(e.target.dataset.num);
		console.log(e.target);
		
		
		switch(e.target.dataset.num){
			case '0':
			store.dispatch({
				type:'index'
			});
			break;
			case '1':
			store.dispatch({
				type:'find'
			});
			break;
			case '2':
			store.dispatch({
				type:'dingdan'
			});
			break;
			case '3':
			store.dispatch({
				type:'wode'
			});
			break;
			case '4':
			store.dispatch({
				type:'search'
			});
			break;
		}
		
	
}
	
	render(){
		return (
			<div className='firstPageTop'>
				<div className="firsttop">
					<div className="Address"><span>#</span><span>广州市人民政府广场</span><span>*</span></div>
					<div className="Weather">
						<div>
							<p>26度</p>
							<p>晴天</p>
						</div>
						<div>太阳</div>
					</div>
				</div>
				<div className="Search" onClick={this.changePage}>
					<div data-num="4">搜索商家 商家名称</div>
				</div>
				<ul className='firstlistNav'>
					<li>蛋糕</li>
					<li>炸鸡</li>
					<li>沙拉</li>
					<li>水果狂欢减20</li>
					<li>粥</li>
					<li>麦当劳</li>
					<li>奶茶</li>
					<li>拉面</li>
					<li>酸菜</li>
					<li>冰咖啡</li>						
				</ul>
				<div className="imgTab">
					<div className="imgListShow">
						<ul className="imgList1">
							<li onClick={this.goPage}><img src="//fuss10.elemecdn.com/b/7e/d1890cf73ae6f2adb97caa39de7fcjpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"/><p>美食</p></li>
							<li onClick={this.goPage}><img src="//fuss10.elemecdn.com/2/35/696aa5cf9820adada9b11a3d14bf5jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"/><p>甜品饮品</p></li>
							<li onClick={this.goPage}><img src="//fuss10.elemecdn.com/c/3c/0184f5b3fa72f295fc01864734218jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"/><p>商超便利</p></li>
							<li onClick={this.goPage}><img src="//fuss10.elemecdn.com/d/49/7757ff22e8ab28e7dfa5f7e2c2692jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"/><p>预定早餐</p></li>							
						</ul>
						<ul className="imgList2">
							<li onClick={this.goPage}><img src="//fuss10.elemecdn.com/c/db/d20d49e5029281b9b73db1c5ec6f9jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"/><p>果蔬生鲜</p></li>
							<li onClick={this.goPage}><img src="//fuss10.elemecdn.com/a/fa/d41b04d520d445dc5de42dae9a384jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"/><p>新店特惠</p></li>
							<li onClick={this.goPage}><img src="//fuss10.elemecdn.com/6/74/785eafaf358fa6b18df37c64c3510jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"/><p>品牌专区</p></li>
							<li onClick={this.goPage}><img src="//fuss10.elemecdn.com/e/89/185f7259ebda19e16123884a60ef2jpeg.jpeg?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/"/><p>晚餐</p></li>							
						</ul>					
					</div>
					<div className="imgListNoshow">
					
					
					</div>

				</div>
				<div className="Dot">
						<div>
							<p></p>
							<p></p>
						</div>
					</div>
				<div className="tjTab">
					<div className="tjTab1">
						<div>
							<h3 className="redFont">限量抢购</h3>
							<p>超级美味 9.9元起</p>
							<p><span className="redFont">91人</span>正在抢<span>{">"}</span></p>
							<img src="//fuss10.elemecdn.com/b/e1/0fa0ed514c093a7138b0b9a50d61fpng.png?imageMogr/format/webp/thumbnail/!240x160r/gravity/Center/crop/240x160/"/>
						</div>
						<div>
							<h3>热卖套餐</h3>
							<p>销量最高 好评最多</p>
							<p>91人正在抢<span>{">"}</span></p>
							<img src="//fuss10.elemecdn.com/d/d4/16ff085900d62b8d60fa7e9c6b65dpng.png?imageMogr/format/webp/thumbnail/!240x160r/gravity/Center/crop/240x160/"/>
						</div>
					</div>
					<div className="tjTab2">
						<div>
							<h3>天天特价</h3>
							<p className="redFont">低至1折</p>
							<img src="//fuss10.elemecdn.com/5/06/ddb537220b8c56d4ef6ae22718984png.png?imageMogr/format/webp/thumbnail/!232x154r/gravity/Center/crop/232x154/"/>
						</div>
						<div>
							<h3>吃货狂欢</h3>
							<p>随叫随到</p>
							<img src="//fuss10.elemecdn.com/a/98/8010e87e876a7bf137597bf7fd324png.png?imageMogr/format/webp/thumbnail/!232x154r/gravity/Center/crop/232x154/"/>
						</div>
						<div>
							<h3>品质优选</h3>
							<p>尖货来袭</p>
							<img src="//fuss10.elemecdn.com/a/b5/dcb5bff74242e06b3c2efee1cbffcpng.png?imageMogr/format/webp/thumbnail/!232x154r/gravity/Center/crop/232x154/"/>
						</div>
					</div>							
				</div>
				<div className="tjShop">
					<h3>推荐商家</h3>
					{this.state.listArr.map(function(item,index,arr){
					return <ul className="listMain" key={index}>
						<li onClick={this.goPage}>
							<div className="listmaintop">
								<img src="https://fuss10.elemecdn.com/1/06/91d6b7d9f13b8c25d2c5e9344bbe4jpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/"/>
								<div className="listmaintoprt">
									<div className="listshopname">
										<div>
											<div className="listpp">品牌</div>
											<h2>{item.name}</h2>
										</div>
										<span>保票</span>
									</div>
									<div className="listzk">
										<div>
											<span>*****</span>
											<span>{item.star}</span>
											<span>月售{item.salesVolumn}单</span>
										</div>
										<div>蜂鸟专送</div>
									</div>
									<div className="listms">
										<div><span>￥{item.startPrice}起送</span><span>|</span><span>配送费￥5</span></div> 
										<div><span>994米</span><span>29分钟</span></div>
									</div>
								</div>
							</div>
							<div className="listmainbottom">
								<p><span><span className="listjian">减</span>满25减12,满50减22,满80减32</span><span>3个活动</span></p>
								<p><span className="listjian">减</span><span>5折限购</span></p>
								
							</div>
						</li>						
					</ul>
				},this)}
				</div>
				<div className="firstPagebottom2"></div>
				<div className="firstPagebottom">
					<div data-num='0' onClick={this.changePage}><p className="fa fa-user-circle-o"></p><p data-num='0'>外卖</p></div>
					<div data-num='1' onClick={this.changePage}><p></p><p data-num='1'>发现</p></div>
					<div data-num='2' onClick={this.changePage}><p></p><p data-num='2'>订单</p></div>
					<div data-num='3' onClick={this.changePage}><p></p><p data-num='3'>我的</p></div>										
				</div>
			
			</div>
		)
	}
}


export default FirstPageTop;



