import React,{Component} from 'react';
import store from '../store.js';


class Shuaixuan extends Component{
	constructor(){
		super()
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
	
	render(){
		return (
			<ul className="paixuul">
			
			<li data-num="3" onClick={this.listPaixu}>
			筛选
			</li>
			<li data-num="4" onClick={this.listPaixu}>
			筛选
			</li>
			<li data-num="5" onClick={this.listPaixu}>
			筛选
			</li>
			
			</ul>
		)
	}
}


export default Shuaixuan;



