import React,{Component} from 'react';
import store from '../store.js';
import data from '../data.js';


class ShopCarPage extends Component{
	constructor(){
		super()
		//data.listPage.meishi[store.getState().shopNum]
//		var arr = data.listPage.meishi[0].showPage.goods.goodsArr;
//		var newArr = [];
//		var priceArr=[];
//		var allPrice="";
		
		this.state ={
			buyCarArr:0,
			pricesArr:[],
			allPrice:[],
			goodNum:data.allNum
//			allPrice:eval(pricesArr.join("+"))
		}
		
		
//		this.state.allPrice=eval(this.state.pricesArr.join("+"));
		console.log(this.state.allPrice);
		console.log(this.state);
		console.log(this.props);
		this.addOne=this.addOne.bind(this);
		this.reduceOne=this.reduceOne.bind(this);
		
		
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'shopPage'
		})
	}
	addOne(e){
		this.setState({
			chooseNum:++(this.state.buyCarArr[e.target.dataset.num].chooseNum),
//			allPrice:eval(this.state.pricesArr.join("+"))
			
			
			
		
		})
		data.allNum++
		this.forceUpdate();
			
	
		
	}
	reduceOne(e){
		this.setState({
			chooseNum:--(this.state.buyCarArr[e.target.dataset.num].chooseNum),
//			allPrice:eval(this.state.pricesArr.join("+"))
			
			
		})
		data.allNum--
//		store.dispatch({
//			type:'shopPage',
//			numId:store.getState().shopNum
//		})
//	this.props.addgoodNum();
		this.forceUpdate();					
	}
	
	render(){
		var arr = data.listPage.meishi[0].showPage.goods.goodsArr;
		data.buyCarArr = [];
		var priceArr=[];
		
		for(let i=0;i<arr.length;i++){
			for(let j=0;j<arr[i].length;j++){
				if(arr[i][j].chooseNum>0){
					data.buyCarArr.push(arr[i][j]);
					priceArr.push(arr[i][j].goodsPrice*arr[i][j].chooseNum)
				}
			}
		}
		this.state ={
			buyCarArr:data.buyCarArr,
			pricesArr:priceArr,
			allPrice:eval(this.state.pricesArr.join("+"))
				
		}
		this.state.allPrice=eval(this.state.pricesArr.join("+"));
		data.allPrice=this.state.allPrice;
		
		return (
			<div className="carPage">
				<div className="carTop">
					<span onClick={this.goPage}>{'<'}</span>
					购物车
					
				</div>	
				{
					data.buyCarArr.length?data.buyCarArr.map(function(item,index,arr){
									return <ul className="carShop" key={index}>
											
											<li>
												
												<div className="carNum"> <img src="https://qlogo2.store.qq.com/qzone/1542376969/1542376969/100?1507981281"/><span>{item.name}</span></div>
												<div className="carNum">
													<span>¥{item.chooseNum*item.goodsPrice}</span>
													<p onClick={this.reduceOne} data-num={index}>-</p>
													<span>{item.chooseNum}</span>
													<p onClick={this.addOne} data-num={index}>+</p>
												</div>
											</li>											
											</ul>
								},this):null
					
						
		
				
				}
		
			
			<p className="carJiesuan"><span className="redFont" style={{fontSize:"20px",marginRight:"20px"}}>结算</span><span className="jg">{this.state.allPrice}</span></p>
		</div>)
	}
}


export default ShopCarPage;






