import React,{Component} from 'react';
import store from '../store.js';
import Goods from './Goods.js';
import Comment from './Comment.js';
import Shop from './Shop.js';
import data from '../data.js';

class ShopPageMain extends Component{
	constructor(){
		super()
		this.state={
			showChildTab:<Goods />,
			goodNum:data.allNum,
			allPrice:data.allPrice,
			index:0
		
		}
		this.addOne=this.addOne.bind(this);
		this.reduceOne=this.reduceOne.bind(this);
		this.tabPage = this.tabPage.bind(this);	

		this.state.goodsArr = data.listPage.meishi[0].showPage.goods.goodsArr[0];
		this.state.chooseNum=data.listPage.meishi[0].showPage.goods.goodsArr[0][0].chooseNum;
	}
	addOne(e){
		if(this.state.goodsArr[e.target.dataset.num].chooseNum>=0&&this.state.goodsArr[e.target.dataset.num].chooseNum<99){
		this.setState({
			chooseNum:++(this.state.goodsArr[e.target.dataset.num].chooseNum),
			
		
		});
		data.allNum++;
		this.state.allPrice=data.allPrice;
		store.dispatch({
			type:'shopPage'
		})
		}
		else{
			this.setState({
			chooseNum:99,
			
		
		})	
		}
		console.log(e.target.dataset.num)
	}
	reduceOne(e){
		if(this.state.goodsArr[e.target.dataset.num].chooseNum>0&&this.state.goodsArr[e.target.dataset.num].chooseNum<99){
		
		this.setState({
			chooseNum:--(this.state.goodsArr[e.target.dataset.num].chooseNum),
			
		})
		store.dispatch({
			type:'shopPage'
		})
		data.allNum--;
		this.state.allPrice=data.allPrice;
		this.forceUpdate();
		console.log(data.allPrice+"aaaa");
		}
		else{
			this.setState({
			chooseNum:0,			
		
		})	
		}		
//		this.forceUpdate();

		
	}
	tabPage(e){

		console.log(e.target.dataset.index);
		switch(e.target.dataset.index){
			case '0':
			this.state.index = 0;
			this.state.showChildTab=<Goods addgoodNum={this.addOne} reducegoodNum={this.reduceOne}/>
			this.forceUpdate();	
			break;
			case '1':
			this.state.index = 1;
			this.state.showChildTab=<Comment />	
			this.forceUpdate();	
			break;
			case '2':
			this.state.index = 2;
			this.state.showChildTab=<Shop />
			this.forceUpdate();	
			break;
			default:
			this.state.showChildTab=<Goods addgoodNum={this.addOne} reducegoodNum={this.reduceOne}/>
			this.forceUpdate();	
			break;
		}
		
	}
	
	
	render(){
		this.state.allPrice=data.allPrice;
		console.log(this.props);
		console.log(this.state.showChildTab)
		if(this.state.index == 0){
			this.state.showChildTab=<Goods addgoodNum={this.addOne} reducegoodNum={this.reduceOne} />
		}
		
		return (
			<div className="shopMain">
				<div className='shopIntroduce'>
					<div className='shopCondition'>
						<img src="https://fuss10.elemecdn.com/1/06/91d6b7d9f13b8c25d2c5e9344bbe4jpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/"/>
						<div>
							<h1>台资味(广州北京路店)</h1>
							<p>
								<span>蜂鸟专送</span>/
								<span>23分钟送达</span>/
								<span>配送费¥5</span>
							</p>
							<div>
								<span>公告:</span>
								<span>收藏店铺有惊喜，点餐随机送饮品，中奖率50%，快来体验哦！用餐高峰期请提前下单，欢迎大佬们的光临！</span>
							</div>
						</div>
					</div>	
					<div className="shopRedspan">
						<div>减</div>满20减18，满35减22，满60减32，满80减42，满110减55
					</div>
				
				</div>
				<div className='tab'>
					<h2 data-index='0' onClick={this.tabPage}>商品</h2>
					<h2 data-index='1' onClick={this.tabPage}>评价</h2>
					<h2 data-index='2' onClick={this.tabPage}>店铺</h2>
				</div>

				
				{this.state.showChildTab}
				
				
				
			</div>
			
		)
	}
}


export default ShopPageMain;



