import React,{Component} from 'react';
import store from '../store.js';
import YouhuiHB from './youhuiHB.js';
import YouhuiDJQ from './youhuiDJQ.js'

class WodeYouhui extends Component{
	constructor(){
		super();
		this.state={
			gopage:<YouhuiHB/>,
			index:"0"
		}
		this.changePage=this.changePage.bind(this)
		this.goPage=this.goPage.bind(this)
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'wode'
		})
	}
	changePage(e){
		if(e.target.dataset.num=="1"){
			this.state.gopage=<YouhuiDJQ />
			this.state.index="1"
				this.forceUpdate();	
				console.log("aaa")
		}
		else{
			this.state.gopage=<YouhuiHB />
			this.forceUpdate();	
			console.log("bbb")}
	
	}
	
	render(){
		if(this.state.index=="0"){
			this.state.gopage=<YouhuiHB />
		}
		return (
			<div className="wodeYouhui">
				<div className="wodeYouhuiTop">
					<span onClick={this.goPage}>{'<'}</span>
					<span>我的优惠</span>
					<span>兑换红包</span>
				</div>
				<ul className="wodeYouhuitab">
					<li onClick={this.changePage} data-num="0">红包<span>0</span>个</li>
					<li onClick={this.changePage} data-num="1">代金券<span>0</span>张</li>		
				</ul>
				{this.state.gopage}
			</div>
			)
	}
}


export default WodeYouhui;



