import React,{Component} from 'react';
import store from '../store.js';
import data from '../data.js';
import ShopPageBuyCar from './shopPageBuyCar.js';

class Goods extends Component{
	constructor(){
		super()
		this.state = {
			allPrice:data.allPrice,
			showObj:data.listPage.meishi[0],
			buyCarArr:0,
			pricesArr:[],
			allPrice:[],
			goodNum:data.allNum,
			goodsType:data.listPage.meishi[0].showPage.goods.goodsType
			
		}
		console.log(store.getState())
		this.state.goodsType=data.listPage.meishi[0].showPage.goods.goodsType;
		this.state.goodsArr = data.listPage.meishi[0].showPage.goods.goodsArr[0];
		this.state.chooseNum=data.listPage.meishi[0].showPage.goods.goodsArr[0][0].chooseNum;
		console.log(this.state.chooseNum)
		
		this.goodsTypeClick = this.goodsTypeClick.bind(this);
		this.carShow=this.carShow.bind(this)
		
	}
	goodsTypeClick(e){
		this.state.arrnum=e.target.dataset.num;
		console.log(this.state.arrnum)
		this.setState({			
			goodsArr:this.state.showObj.showPage.goods.goodsArr[e.target.dataset.num]
		})
		this.forceUpdate();
	}

	carShow(){
		document.querySelector("#show").style.display="flex";
	}
	goPay(){
		
		store.dispatch({
			type:'buyCar'
		})
	}
	render(){
//		this.state.allPrice=data.allPrice;
		console.log(this.state.showObj);
		console.log(this.state.goodsType);
		console.log(this.state.goodsArr);
		var arr = data.listPage.meishi[0].showPage.goods.goodsArr;
		var newArr = [];
		var priceArr=[];
		
		for(let i=0;i<arr.length;i++){
			for(let j=0;j<arr[i].length;j++){
				if(arr[i][j].chooseNum>0){
					newArr.push(arr[i][j]);
					priceArr.push(arr[i][j].goodsPrice*arr[i][j].chooseNum)
				}
			}
		}
	
		this.state.allPrice=eval(priceArr.join("+"));
		data.allPrice=this.state.allPrice
		return (
			<div className="Goods">
			<div className="goods">
				<div className='typeList'>
					{
						this.state.goodsType.map(function(item,index,arr){
							return <div onClick={this.goodsTypeClick} key={index} data-num={index} className='typeListItem'>{item}</div>
						},this)
					}
					
				
				</div>
				<div className='goodsList'>
					{
						this.state.goodsArr.map(function(item,index,arr){
							return <div onClick={this.goodsClick} key={index} data-num={index} className='goodsListItem'>
											<div className='goodsIntroduce' >
												<div className='goodsCondition'>
													<img src="https://fuss10.elemecdn.com/1/06/91d6b7d9f13b8c25d2c5e9344bbe4jpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/"/>
													<div>
														<h2>{item.name}</h2>
														<div>{item.chief}</div>
														<p>
															<span>月售{item.salesVolumn}份</span>
															<span>好评率90%</span>
									
														</p>
														<div className="desc">
															<div className="zk">3.3折</div>
															<div className="xlqg">限量抢购</div>
															<div className="goodNum">剩{item.chooseNum}份</div>
						
														</div>
									
														<div className="goodsPrice">
															<div className="goodPrice">¥{item.goodsPrice}<span>¥40</span></div>	
															<div className="goodZJ" >
																<div className="addGood" onClick={this.props.reducegoodNum}  data-num={index}>-</div>
																<div>{item.chooseNum}</div>
																<div className="addGood" onClick={this.props.addgoodNum} data-num={index}>+</div>
															</div>
														</div>
													</div>
												</div>	
											</div>
									</div>
						},this)
						
					}
				
				
				</div>
				</div>
				<ShopPageBuyCar addgoodNum={this.props.addgoodNum} reducegoodNum={this.props.reducegoodNum}/>
				<div className="shopPageBuy">
					<div>
						<div className="shopCar iconfont icon-gouwuchekong" onClick={this.carShow}>
						
						</div>
						<div className="shopNum">
							{data.allNum}
						</div>
						<div>
							<div>¥{this.state.allPrice}</div>
							<div>配送费¥5元</div>
							
							
						</div>
					</div>
					
					<div onClick={this.goPay} className="shopGo">
							去结算
					</div>
				</div>
			
			
			
			
			</div>
			
			
		)
	}
}


export default Goods;



