import React,{Component} from 'react';
import store from '../store.js';


class FirstPageBottom extends Component{
	constructor(){
		super()
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
	
	render(){
		return (
			<div className='shopPageTop'>
		
			</div>
		)
	}
}


export default FirstPageBottom;



