import React,{Component} from 'react';
import store from '../store.js';


class WodeAddress extends Component{
	constructor(){
		super()
		this.goPage=this.goPage.bind(this)
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'wode'
		})
	}
	
	render(){
		return (
			<div className="findPage">
				<div className="findTop">
				<span onClick={this.goPage}>{'<'}</span>
				我的地址
				</div>
				<div>
					<div>
					<p>
						<span>艳红姐</span>
						<span>女士</span>
						<span>178739589468</span>
					</p>
					<p>
						<span>学校</span>
						<span>湖南工学院衡花路18号D4217</span>
					</p>
					</div>
					<span>$</span>
				</div>
			</div>
			)
	}
}


export default  WodeAddress ;



