import {createStore} from 'redux';


var reducer = function(state,action){
	
	var newState = {
		
		viewCom:'index',
		
	};
	
	if(action.type){
		newState.viewCom = action.type;
		newState.shopNum = action.numId;
	}
	console.log(action.type)
	
	
	return newState;
}
var store = createStore(reducer);

export default store;



