import React,{Component} from 'react';
import store from '../store.js';
import data from '../data.js';


class ShopPageBuyCar extends Component{
	constructor(){
		super()
				
		this.state ={
			buyCarArr:0,
			biaoji:true,
			pricesArr:[],
			allPrice:""
		}
		console.log(this.state);
		console.log(this.props);

		this.carNoshow=this.carNoshow.bind(this);		
		
	}
	

	carNoshow(e){
		console.log(e.target.parentElement.parentElement)
		e.target.parentElement.parentElement.style.display="none"
		
		this.forceUpdate();	
		
			
	}
	
	
	render(){
		var arr = data.listPage.meishi[0].showPage.goods.goodsArr;
		var newArr = [];
		var priceArr=[];
				
		for(let i=0;i<arr.length;i++){
			for(let j=0;j<arr[i].length;j++){
				if(arr[i][j].chooseNum>0){
					newArr.push(arr[i][j]);
					priceArr.push(arr[i][j].goodsPrice*arr[i][j].chooseNum)
				}
			}
		}
		this.state ={
			buyCarArr:newArr,
			pricesArr:priceArr,
			allPrice:eval(this.state.pricesArr.join("+"))
				
		}
		
		console.log(this.state.pricesArr);
		this.state.allPrice=eval(this.state.pricesArr.join("+"));
			
		console.log(this.state.allPrice);
		
		data.allPrice=this.state.allPrice;
		console.log(data.allPrice)
		
		if(this.state.buyCarArr.length){
			
		return (
			<div className="carPage" id="show">
				<div className="small">
						<span onClick={this.carNoshow}>×</span>
						<span>购物车</span>
						
						<span>清空</span>
				</div>	
		{
			this.state.buyCarArr.length?this.state.buyCarArr.map(function(item,index,arr){
				return <ul className="carShop" key={index}>

						<li>
							<div>{item.name}</div>
							<div className="carNum">
								<span>¥{item.chooseNum*item.goodsPrice}</span>
								<p onClick={this.props.reducegoodNum} data-num={index}>-</p>
								<span>{item.chooseNum}</span>
								<p onClick={this.props.addgoodNum} data-num={index}>+</p>
							</div>
						</li>
						</ul>
			},this):null
		}
		</div>)
		

		
		}
		else{
			return <div></div>
		}
	}
}


export default ShopPageBuyCar;


/*
 * ReactDOM.render可是使页面重新渲染，但是没有变更数据（props以及state），它就不会更新
 * 如果你的数据在一个大组件中通用，那么直接将这一部分数据，给大组件的state,
 * 如何在子组件里面更新大组件的state ,那么是将大组件的方法函数传递给我们的子组件的props,然后将props的函数传递给交互事件;
 * 
 * */



