import React,{Component} from 'react';
import store from '../store.js';
import datatext from '../dataTest.js';

class Paixu extends Component{
	constructor(){
		super()
		this.state={
			listArr:datatext.listPage.meishi,
			showList:""
		}
		this.listPaixu=this.listPaixu.bind(this)
		
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
	listPaixu(e){
			switch(e.target.dataset.num){
			case '3':
			datatext.listPage.meishi.sort(function(a,b){
				return a.star<b.star
			})
			store.dispatch({
			type:'listPage'
			})
			this.forceUpdate();
			console.log("aaa")
	
			break;
			case '4':
			datatext.listPage.meishi.sort(function(a,b){
				return a.salesVolumn<b.salesVolumn
			})
			store.dispatch({
			type:'listPage'
			})
			this.forceUpdate();
			console.log("bbb")
	
			break;
			case '5':
			datatext.listPage.meishi.sort(function(a,b){
					return a.startPrice>b.startPrice
				})
			store.dispatch({
			type:'listPage'
			})
			this.forceUpdate();
			console.log("ccc")
			break;
		}
	}
	render(){
		
		return (
			<ul className="paixuul">
			
			<li data-num="3" onClick={this.listPaixu}>
			按照star排序
			</li>
			<li data-num="4" onClick={this.listPaixu}>
			按照月售量
			</li>
			<li data-num="5" onClick={this.listPaixu}>
			按照起送价
			</li>
			
			</ul>
		)
	}
}


export default Paixu;



