import React,{Component} from 'react';
import store from '../store.js';


class Shop extends Component{
	constructor(){
		super()
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
	
	render(){
		return(
			<div className="dianpu">
				<div className="shopActive">
					<h2>活动与服务</h2>
					<p>
						<span className="redFont pd">减</span>
						<span>满25减10，满45减15，满78减20</span>
					</p>
					<p>
						<span className="green pd">首</span>
						<span>新用户下单立减17元（不与其它活动同享）</span>
					</p>
					<p>
						<span  className="pd">保</span>
						<span>已加入“外卖保”计划，食品安全有保障</span>
					</p>
				
				</div>
				<div className="shopImg">
					<h2>商家实景</h2>
					<div><img src="https://qlogo2.store.qq.com/qzone/1542376969/1542376969/100?1507981281"/><img src="https://qlogo2.store.qq.com/qzone/1542376969/1542376969/100?1507981281"/></div>
				</div>
			
				<div className="shopAbout">
					<h2>商家信息</h2>
					<p className="shopBet"><span>商家电话</span><span>24536778{">"}</span></p>
					<p className="shopBet"><span>地址</span><span>广州越秀区中山五路</span></p>
					<p><span>营业时间</span><span>10:30-22:00</span></p>				
				</div>
				<div className="shopBet"><h2>营业资质</h2><span>{">"}</span></div>
		</div>
		)
	}
}


export default Shop;



