import React,{Component} from 'react';
import store from './store.js';
import FirstPageTop from './firstPage/firstPageTop.js';
import FirstPageMain from './firstPage/firstPageMain.js';
import FirstPageBottom from './firstPage/firstPageBottom.js';

class ElmIndex extends Component{
	constructor(){
		super()
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
	
	render(){
		return (
			<div>
				<FirstPageTop />
				<FirstPageMain />
				<FirstPageBottom />
			</div>
		)
	}
}


export default ElmIndex;



