import React,{Component} from 'react';
import store from '../store.js';


class YouhuiDJQ extends Component{
	constructor(){
		super();
		this.goPage=this.goPage.bind(this)
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'wode'
		})
	}
	
	render(){
		return (
				<div className="youhuiHBbottom">
					<img src="//fuss10.elemecdn.com/5/51/255490294be88d8a2fbb92c5ad1bcgif.gif"/>
					<p>没有商家代金券</p>
					<div>去领券中心看看</div>					
				</div>
			)
	}
}


export default YouhuiDJQ  ;



