import React,{Component} from 'react';
import store from '../store.js';


class Dingdan extends Component{
	constructor(){
		super()
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'index'
		})
	}
	
	render(){
		return (
			<div className="dingdanPage">
				<div className="dingdanTop">
				<span onClick={this.goPage}>{'<'}</span>
				订单
				</div>
				<div className="wuD">
					<img src="//fuss10.elemecdn.com/6/87/4efda8c6bf4734d39faf86fe190c3gif.gif"/>
					<p>近三个月无外卖订单记录</p>
				</div>
			</div>
		)
	}
}


export default Dingdan;



