import React,{Component} from 'react';
import store from '../store.js';


class JinbiSC extends Component{
	constructor(){
		super();
		this.goPage=this.goPage.bind(this)
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'wode'
		})
	}
	
	render(){
		return (
			<div className="jinbiSC">
				<div className="jinbiSCTop">
				<span onClick={this.goPage}>{'<'}</span>
				这是金币商城
				</div>
			</div>
			)
	}
}


export default JinbiSC ;



