import React,{Component} from 'react';
import store from '../store.js';


class ShopPageTop extends Component{
	constructor(){
		super()
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'index'
		})
	}
	carPage(){
		store.dispatch({
			type:'buyCar'
		})
	}
	
	render(){
		return (
			<div className='shopPageTop'>
				<h1 className='shopPageTopNav'>
					<span className='back' onClick={this.goPage}>{'<'}</span>
					<span className='iconfont icon-gouwuchekong' onClick={this.carPage}></span>
				</h1>
			
			</div>
		)
	}
}


export default ShopPageTop;



