import React,{Component} from 'react';
import store from './store.js';
import datatext from './dataTest.js';
import Shuaixuan from './listPage/shuaixuan.js';
import Fenlei from './listPage/fenlei.js';
import Paixu from './listPage/paixu.js';

class ListPage extends Component{
	constructor(){
		super();
		this.state={
			listArr:datatext.listPage.meishi,
			showList:""
		}
		console.log(this.state.listArr)
		this.listStar=this.listStar.bind(this)
		this.listtabPage=this.listtabPage.bind(this)
	}
	listtabPage(e){
		console.log(e.target.dataset.num);
		switch(e.target.dataset.num){
			case '0':
			this.setState({
				showList:<Fenlei />
			});
			break;
			case '1':
			this.setState({
				showList:<Paixu />
			});
			break;
			case '2':
			this.setState({
				showList:<Shuaixuan />
			});
			break;
		}
		
	}
	goPage(e){
		store.dispatch({
			type:'shopPage',
			numId:e.target.dataset.num
		})
	}
	goPage2(e){
		store.dispatch({
			type:'index',
			numId:e.target.dataset.num
		})
	}
	listStar(){
		this.state.listArr.sort(function(a,b){
			return a.star>b.star
		})
			this.forceUpdate();
			console.log("aaa")
	}
	
	render(){
		this.state.listArr=datatext.listPage.meishi
		return 	(
				<div className="listPage">
					<div className="listTop">
						<span onClick={this.goPage2}>{'<'}</span>
						美食
					</div>
					<ul className="listTab">
						<li data-num="0" onClick={this.listtabPage}>分类</li>
						<li data-num="1" onClick={this.listtabPage}>排序</li>
						<li data-num="2" onClick={this.listtabPage}>筛选</li>
					</ul>
					{this.state.showList}
				{this.state.listArr.map(function(item,index,arr){
					return <ul className="listMain" key={index}>
						<li onClick={this.goPage}>
							<div className="listmaintop">
								<img src="https://fuss10.elemecdn.com/1/06/91d6b7d9f13b8c25d2c5e9344bbe4jpeg.jpeg?imageMogr/format/webp/thumbnail/!130x130r/gravity/Center/crop/130x130/"/>
								<div className="listmaintoprt">
									<div className="listshopname">
										<div>
											<div className="listpp">品牌</div>
											<h2>{item.name}</h2>
										</div>
										<span>保票</span>
									</div>
									<div className="listzk">
										<div>
											<span>*****</span>
											<span>{item.star}</span>
											<span>月售{item.salesVolumn}单</span>
										</div>
										<div>蜂鸟专送</div>
									</div>
									<div className="listms">
										<div><span>￥{item.startPrice}起送</span><span>|</span><span>配送费￥5</span></div> 
										<div><span>994米</span><span>29分钟</span></div>
									</div>
								</div>
							</div>
							<div className="listmainbottom">
								<p><span><span className="listjian">减</span>满25减12,满50减22,满80减32</span><span>3个活动</span></p>
								<p><span className="listjian">减</span><span>5折限购</span></p>
								
							</div>
						</li>						
					</ul>
				},this)}
			</div>)
	}
}


export default ListPage;


