import React from 'react';
import ReactDOM from 'react-dom';
import './css/index.css';
import './css/iconfont.css';
import store from './js/store.js';

import ElmIndex from './js/ElmIndex.js'
import ListPage from './js/ListPage.js'
import ShopPage from './js/ShopPage.js'
import ShopCarPage from './js/shopPage/shopCarPage.js'
import Find from './js/firstPage/find.js';
import Dingdan from './js/firstPage/dingdan.js';
import Wode from './js/firstPage/wode.js';
import Search from './js/firstPage/search.js';
import DownApp from './js/wodePage/downApp.js';
import JinbiSC from './js/wodePage/jinbiSC.js';
import MyServer from './js/wodePage/myServer.js';
import ShareApp from './js/wodePage/shareApp.js';
import WodeAddress from './js/wodePage/wodeAddress.js';
import WodeJinbi from './js/wodePage/wodeJinbi.js';
import WodeYouhui from './js/wodePage/wodeYouhui.js';
import WodeZhanghao from './js/wodePage/wodeZhanghao.js';








var viewComName,viewCom=<ElmIndex  />;

var togglePage = function(){
	console.log(store.getState());
	viewComName=store.getState().viewCom;
	switch(viewComName){
		case 'index':
		 	viewCom = <ElmIndex  />
		 	break;
		case 'listPage':
		 	viewCom = <ListPage  />
		 	break;
		case 'shopPage':
		 	viewCom = <ShopPage  obj={store.getState()} />
		 	break;
		case 'buyCar':
		 	viewCom = <ShopCarPage  />
		 	break;
		case 'find':
		 	viewCom = <Find />
		 	break;
		 case 'search':
		 	viewCom = <Search />
		 	break;
		 case 'wode':
		 	viewCom = <Wode />
		 	break;
		 case 'downApp':
		 	viewCom = <DownApp />
		 	break;		 	
		 case 'jinbiSC':
		 	viewCom = <JinbiSC />
		 	break;		 	
		 case 'myServer':
		 	viewCom = <MyServer />
		 	break;		 	
		 case 'shareApp':
		 	viewCom = <ShareApp/>
		 	break;		 	
		 case 'wodeAddress':
		 	viewCom = <WodeAddress/>
		 	break;		 	
		 	
		 case 'wodeJinbi':
		 	viewCom = <WodeJinbi/>
		 	break;		 	
		 	
		 case 'wodeYouhui':
		 	viewCom = <WodeYouhui/>
		 	break;		 	
		 	
		 case 'wodeZhanghao':
		 	viewCom = <WodeZhanghao/>
		 	break;		 			 	
		 case 'dingdan':
		 	viewCom = <Dingdan/>
		 	break;
		 default:
			viewCom = <ElmIndex  />
		 	break;
	}
}

store.subscribe(function(){
	togglePage();
	ReactDOM.render(viewCom,document.getElementById('root'))
})
ReactDOM.render(viewCom,document.getElementById('root'))



